module.exports = "<div class=\"tab view\" id=tab4><div class=content><header class=\"bar bar-nav\"><h1 class=title>购物车</h1></header><div class=content><div id=no-ware v-if=!user.shopcar.length transition=fade><span class=\"icon icon-cart\"></span><h4>购物车空空如也</h4></div><div v-else=\"\" transition=fade><header class=\"bar bar-nav bar-sec\"><span class=col-50><span class=\"icon icon-app\"></span> 方形蜡烛<span class=\"icon icon-check\"></span></span><span class=col-50><span class=\"icon icon-computer\"></span> 餐具套餐<span class=\"icon icon-check\"></span></span></header><div class=content id=shopcar-list data-type=js><div class=content-inner><ul class=\"order-list list-block media-list\"><li v-for=\"ware in user.shopcar\" data-shopcar-id={{$index}} transition=fade><label class=\"label-checkbox item-content\"><div class=item-media><i class=\"icon icon-form-checkbox\" v-bind:class=\"{'button-checked': ware.state}\" v-on:click=check></i></div><div class=item-media><img v-bind:src=ware.info.img /></div><div class=item-inner><div class=item-title>{{ware.info.nameEn}}</div><div class=item-subtitle>{{ware.info.name}}</div><div class=item-subtitle>重量：{{ware.weight}}磅</div><div class=\"item-text pull-left\">￥{{ware.info.price*ware.sum*ware.weight}}</div><div class=pull-right data-shopcar-id={{$index}}><span class=\"icon icon-down\" v-on:click=sumChange(+1,$event)></span> {{ware.sum}}<span class=\"icon icon-up\" v-on:click=sumChange(-1,$event)></span></div></div></label></li></ul></div></div></div></div></div><div id=buy-now v-if=user.shopcar.length transition=fade><div class=\"list-block media-list pull-left\"><label class=\"label-checkbox item-content\"><div class=item-media v-on:click=checkAll><i class=\"icon icon-form-checkbox\"></i></div><div class=item-inner><div class=\"item-title pull-left\">全选</div><div class=pull-right>￥198</div></div></label></div><div class=order v-on:click=shopcarBuy>购买</div></div></div>";


